
<template>
	<div class='header'>
  	<span class="back-btn" @click="goBack()">←</span>
  	<h6 class="header-name" v-text="title"></h6>
  	<span class="login" @click='Tologin(type)'><i class=""></i>{{login}}</span>
  </div>
</template>

<script>
import {setCookie,getCookie} from '@/assets/js/cookieUtil'
 export default{
   data(){
   	  return{
   	  	title: '购车惠',
        login: '',
        type:'',
        phone: ''
   	  }
   },
   methods:{
   	  goBack(){
   	  	 history.back(-1);
   	  },
   	  Tologin(type){
        this.type = type;
        if(type == 1){
          this.$router.push('/loginList');
        }else{
          this.$router.push('/orderList');
        }
      	 	
      	 },
   },
   mounted () {
      if (this.isLogin==undefined||this.isLogin=="") {
        this.login = '登录';
        this.type = 1
      } else {
        // 执行后续操作
        this.login = '主页';
        }
    },
    computed: {
      isLogin () {
        // return this.$store.getters.getLogin;
        this.phone = getCookie("phone");
       // console.log(this.userId);
        return this.phone;
      }
    }
 }
</script>

<style scoped>
  .header{
	max-width: 640px;
	position: relative;
	background: -webkit-linear-gradient(left,#ee0c41,#fc3b3f);
	background: #ff3938;
	color: #fff;
	height: 2.55rem;
	line-height: 2.55rem;
}
.header h6{
	width: 70%;
	font-weight: bold;
	text-align: center;
	font-size: 1.1rem;
	letter-spacing: 1px;
	color: #fff;
}
.header span{
	display: block;
	width: 10%;
	float: left;
	text-align: center;
}
.login{
	position: absolute;
    top: 0;
    right: 10px;
    cursor: pointer;
}
/*header end*/
</style>


